.single-post .post-navigation {
    font-size: 1.125rem; /*18px;*/
}

.comment-navigation,
.posts-navigation,
.post-navigation {

    .site-main & {
        margin: 0 0 1.5em;
        overflow: hidden;

    }

    .nav-previous {
        float: left;
        width: 50%;
        white-space:normal;
/*        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;*/
    }

    .nav-next {
        float: right;
        text-align: right;
        width: 50%;
        white-space:normal;
/*        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;*/
    }
}

.nav-previous, .nav-next {
    width: 47%;
    position: relative;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /*color: #fff;
    background-color: #007bff;
    border-color: #007bff;
    @include tsk_links();*/

    a {
        color: inherit;
        text-decoration: none !important;
        background-color: transparent;
        &:hover {
            color: #c6c6c6; /*#eeeeee;*/
        }
        margin-right: 5px;
        font-weight: 700;
        border-radius: 5px;
    }
    /*overflow: hidden;*/
    white-space: nowrap;
    text-overflow: ellipsis;
}

.nav-previous {
    float: left;
    padding-left: 0;
    padding-right: 25px;
    a:before {
        content: "\f04a";  /* this is your text. You can also use UTF-8 character codes as I do here */
        font-family: "Font Awesome 5 Free";
        left:-10px;
        position:relative;
        top:0;
    }
}

.nav-next {
    float: right;
    padding-left: 25px;
    padding-right: 0px;
    text-align: right;

    a:after {
        content: "\f04e";  /* this is your text. You can also use UTF-8 character codes as I do here */
        font-family: "Font Awesome 5 Free";
        right:-10px;
        position:relative;
        top:0;
    }
}
